<template>
  <div class="xqlist">
    <!--  -->
        <div class="msg_heard">
            <div @click="back">
                <van-icon name="arrow-left" color="#fff"  />
            </div>
            <div><p class="p"></p></div>
            <div></div>
        </div>
        <!--  -->
        <div class="content_title"><h2>{{arr.title}}</h2></div>
        <!--  -->
        <div class="content_author">
          <div class="content_left">
              <div>
                <van-image
                  round
                  width="40px"
                  height="40px"
                  :src='arr.avatar'
                  @error="imgError"
                />
              </div>
              <!--  https://img01.yzcdn.cn/vant/cat.jpeg-->
              <div class="content_time">
                <p class="author">:{{arr.author}}</p>
                <p class="time">{{new Date(arr.time).toLocaleDateString() }}</p>
              </div>
          </div>
          <div>
            <van-button icon="plus" round  type="primary" class="btn_color" @click="isshow">{{show1?'':''}}</van-button>
          </div>
        </div>
        <!--  -->
        <div class="neirong">
          <p>{{arr.content}}</p>
          <div v-for="(item,i) in arr.imageSrc" :key="i">
            <img :src="item" alt="" srcset="" @error="imgError" />
          </div>
        </div>
        <!--  -->
        <div v-for="(item,i) in pinglunlist" :key="i">
          <div class="content_author1" >
            <div class="content_left">
              <div>
                <van-image round width="40px" height="40px" :src='item.info.avatar' />
              </div>
              <!--  https://img01.yzcdn.cn/vant/cat.jpeg-->
              <!--  -->
              <div class="pinglun">
                <van-cell >
                    <div class="content_time" @click="huifupinlun(item)">
                      <p>:{{item.info.nickname}}</p>
                      <h6>{{item.content}}</h6>
                      <div class="content_btn">
                        <p class="time">{{ontime(item.create_time) }}</p>
                        <button class="huifu" @click.stop="chakanhuifu(item)"> {{item.reply_num}}</button>
                      </div>
                    </div>
                </van-cell>
                <van-popup v-model="showshow"  closeable position="bottom"  :style="{ height: '30%' }">
                  {{nickname}}
                  <van-field
                    v-model="message"
                    autosize
                    autofocus
                    type="textarea"
                    maxlength="1500"
                    placeholder=""
                    show-word-limit
                    class="text"
                  />
                  <div class="baocun">
                    <van-button type="primary" block  @click="gethuifupinglun" ></van-button>
                  </div>
                </van-popup>
              </div>
              

            </div>
        
            <div >
              <van-icon name="good-job" size="20"  @click="qxhuifudianzan(item)" color="red"   v-if="item.is_like"/>
              <van-icon name="good-job" size="20"  @click="huifudianzan(item)" v-else/>
              <span>{{item.like_count}}</span>
            </div>
          </div>
          <div v-show="item._id===clickid">
            <!-- // -->
            <div class="content_author2" v-for="(item1,i) in huifulist" :key="i">
              <div class="content_left">
                <div>
                  <van-image round width="40px" height="40px" :src='item1.info.avatar' />
                </div>
                <!--  https://img01.yzcdn.cn/vant/cat.jpeg-->
                <div class="content_time">
                  <p>:{{item1.info.nickname}}</p>
                  <h6>{{item1.content}}</h6>
                  <div class="content_btn">
                    <p class="time">{{ontime(item1.create_time)}}</p>
                    <!-- <button class="huifu"></button> -->
                  </div>
                </div>
              </div>
              <!-- <div> 
                <span>{{item1.like_count}}</span>
                <van-icon name="good-job" />
              </div> -->
            </div>
          
          </div>
        </div>
        
        <!--  -->
        <div class="xq_footer" @click="islogin">
          <div class="pinglun">
            <van-cell >
                  <van-button round type="info" class="btn" @click="showPopup"></van-button>
            </van-cell>
            <van-popup v-model="show"  closeable position="bottom"  :style="{ height: '40%' }">
              
              <van-field
                v-model="message"
                autosize
                autofocus
                type="textarea"
                maxlength="1500"
                placeholder=""
                show-word-limit
                class="text"
              />
              <div class="baocun">
                <van-button type="primary" block  @click="getFabupinglun" ></van-button>
              </div>
            </van-popup>
          </div>
          <!--  -->
          <van-icon name="comment-o" size="20"  />
          <!--  -->
          <div>
            <van-icon name="star" size="20"  @click="qxshoucang" color="red" v-if="arr.is_fav"/>
            <van-icon name="star" size="20"  @click="shoucang" v-else/>
          </div>
          
          <!--  -->
          <div>
            <van-icon name="good-job" size="20"  @click="qxdianzhan" color="red"   v-if="arr.is_like"/>
            <van-icon name="good-job" size="20"  @click="dianzhan" v-else/>
          </div>
          
          <!--  -->
          <div>
            <van-cell  @click.stop="showShare = true" >
              <van-icon name="share" size="20" />
            </van-cell>
            
            <van-share-sheet
              v-model="showShare"
              title=""
              :options="options"
              @select="onSelect"
            />
          </div>
          
        </div>
        <div>
         
        </div>
  </div>
</template>

<script>
import { Cell, CellGroup ,Icon,Button,Popup,Image as VanImage,Field,Toast,Collapse, CollapseItem ,ShareSheet  } from 'vant';
import axios from 'axios'
import QRCode from 'qrcode'
export default {
  name:'listcontent',
  data() {
    return {
        arr:{},
        show1:true,
        show:false,
        value: '',
        message:'',
        pinglunlist:[],
        clickid:'',
        huifulist:[],
        showshow:false,
        huifuid:'',
        showShare: false,
        nickname:'',
        options: [
              { name: '', icon: 'wechat' },
              // { name: '', icon: 'wechat-moments' },
              { name: '', icon: 'weibo' },
              { name: 'QQ', icon: 'qq' },
              { name: '', icon: 'link' },
              { name: '', icon: 'qrcode' },  
          ],
      };
  },
  components:{
    [Cell.name]:Cell,
    [CellGroup.name]:CellGroup,
    [Icon.name]:Icon,
    [Button.name]:Button,
    [VanImage.name]:VanImage,
    [Popup.name]:Popup ,
    [Field.name]:Field,
    [Toast.name]:Toast,
    [Collapse.name]:Collapse,
    [CollapseItem.name]:CollapseItem, 
    [ShareSheet.name]:ShareSheet  
   },
  methods:{
    back(){
        this.$router.go(-1);
    },
    islogin(){
      let uid =localStorage.getItem('uid');
      let token =localStorage.getItem('token');
      let tokenExpired =localStorage.getItem('tokenExpired')
      if(uid&&token){
        let now = +new Date() ;
         if(now>=tokenExpired){
            Toast('')  
         }else{
          
         }
      }else{
        Toast('')
      }
    },
    isshow(){
      this.show1=!this.show1
      
    },
    // 
    chakanhuifu(item){
       this.clickid=item._id

       if(item.reply_num===0){
        this.clickid=''
       }else{
          axios({
                method:'post',
                url:'https://d33a5037-6c63-4e92-8bb1-30018ab701ea.bspapp.com/http/api/get_reply_list',
                data:{
                      "skip": 0, //
                      "limit": 10, //
                      "user_id": localStorage.getItem('uid'), //ID
                      "reply_comment_id": item._id, //ID comment_type1  comment_type0
                      "article_id": this.$route.query.id,//ID
                    }    
                }).then(res=>{
                    this.huifulist=res.data.data
                    // console.log("",this.huifulist);
          })
       }
    },
    // 
    showPopup() {
      this.show = true;
    },
    // 
    getFabupinglun(){
      axios({
        method:'post',
        url:'https://d33a5037-6c63-4e92-8bb1-30018ab701ea.bspapp.com/http/api/add_comment',
        data:{
          "user_id": localStorage.getItem('uid'), //ID
          "article_id": this.$route.query.id, //ID
          "content": this.message, //
          "reply_comment_id": "", //ID comment_type1  comment_type0
          "comment_type": 0 //01
        }
      }).then(res=>{
        this.show = false
        
        this.message=''
        this.pinglunliebiao()
        // console.log('',res);
      })
    },
    // 
    huifupinlun(item){
      // console.log(1,item._id);
      this.huifuid=item._id
      this.nickname=item.info.nickname
      this.showshow=true
      
    },
    // 
    gethuifupinglun(){
      
      axios({
        method:'post',
        url:'https://d33a5037-6c63-4e92-8bb1-30018ab701ea.bspapp.com/http/api/add_comment',
        data:{
          "user_id": localStorage.getItem('uid'), //ID
          "article_id": this.$route.query.id, //ID
          "content": this.message, //
          "reply_comment_id": this.huifuid, //ID comment_type1  comment_type0
          "comment_type": 1 //01
        }
      }).then(res=>{ 
        this.showshow = false
        // Toast(res.data.mag)
        this.message='';
        this.pinglunliebiao();
        // console.log('',res);
      })
    },
    // 
    pinglun(){
      // console.log(this.$route.query.id);
        axios({
              method:'post',
              url:'https://d33a5037-6c63-4e92-8bb1-30018ab701ea.bspapp.com/http/api/get_article_detail',
              data:{
                      "article_id":this.$route.query.id,
                      'user_id':localStorage.getItem('uid')
                  }    
              }).then(res=>{
                // if(!res.data.data){
                //   this.arr=[]
                //   console.log(1);
                // }
                  this.arr=res.data.data
                  console.log("",res.data);
                  // console.log(typeof(this.arr));
        })
        
    },
    // 
    pinglunliebiao(){
      axios({
              method:'post',
              url:'https://d33a5037-6c63-4e92-8bb1-30018ab701ea.bspapp.com/http/api/get_comment_list',
              data:{
                      "article_id":this.$route.query.id,
                      'user_id':localStorage.getItem('uid'),
                      "skip":0,
                      "limit":10 
                  }    
              }).then(res=>{
                  this.pinglunlist=res.data.data
                  // console.log('',res);
                  // console.log('',this.pinglunlist);
        })
    },
    // 
    dianzhan(){
      axios({
        method:'post',
        url:'https://d33a5037-6c63-4e92-8bb1-30018ab701ea.bspapp.com/http/api/like',
        data:{
             'user_id':localStorage.getItem('uid'),
             'article_id':this.$route.query.id
        }
      }).then(res=>{
        this.pinglun()
        // console.log('',res);
      })
    },
    // 
    qxdianzhan(){
      axios({
        method:'post',
        url:'https://d33a5037-6c63-4e92-8bb1-30018ab701ea.bspapp.com/http/api/unlike',
        data:{
             'user_id':localStorage.getItem('uid'),
             'article_id':this.$route.query.id
        }
      }).then(res=>{
        this.pinglun()
        // console.log('',res);
      })
    },
    // 
    huifudianzan(item){
      // console.log(item._id);
      axios({
          method:'post',
          url:'https://d33a5037-6c63-4e92-8bb1-30018ab701ea.bspapp.com/http/api/comment_like',
          data:{
              'user_id':localStorage.getItem('uid'),
              'comment_id':item._id
          }
        }).then(res=>{
          this.pinglunliebiao()
          // console.log('',res);
        })
    },
    // 
    qxhuifudianzan(item){
        axios({
          method:'post',
          url:'https://d33a5037-6c63-4e92-8bb1-30018ab701ea.bspapp.com/http/api/comment_unlike',
          data:{
              'user_id':localStorage.getItem('uid'),
              'comment_id':item._id
          }
        }).then(res=>{
          this.pinglunliebiao()
          // console.log('',res);
        })
    },
    //  
    shoucang(){
        axios({
          method:'post',
          url:'https://d33a5037-6c63-4e92-8bb1-30018ab701ea.bspapp.com/http/api/add_fav',
          data:{
              'user_id':localStorage.getItem('uid'),
              'article_id':this.$route.query.id
          }
        }).then(res=>{
          this.pinglun()
          // console.log('',res);
        })
    },
    // 
    qxshoucang(){
        axios({
          method:'post',
          url:'https://d33a5037-6c63-4e92-8bb1-30018ab701ea.bspapp.com/http/api/remove_fav',
          data:{
              'user_id':localStorage.getItem('uid'),
              'article_id':this.$route.query.id
          }
        }).then(res=>{
          this.pinglun()
          // console.log('',res);
        })
    },
    // 
    onSelect(option) {
      // this.showshow=false
      let {icon} =option;
      let obj = {
        'weibo':this.weibo,
        'wechat':this.wechat,
        'qq':this.qq,
        'link':this.link,
        'qrcode':this.qrcode
      }
      obj[icon]()
      this.showShare = false;
    },
    weibo(){
      console.log(this.arr);
      let titles =this.arr.title
      let imgsrc =this.arr.imageSrc[0]
      window.open(
        `https://service.weibo.com/share/share.php?
         title=${titles}(  @ App:https://xpcll.github.io/ )
         &pic=${imgsrc}`
      )
      
      //  console.log(22);
    },
    wechat(){
     console.log(11);
     this.erweima('60%','')
    },
    qq(){
      let titles =this.arr.title
      let imgsrc =this.arr.imageSrc[0]
      window.open(
        `https://connect.qq.com/widget/shareqq/index.html?
         url=${location.href}
         &title=${titles}(  @ App:https://xpcll.github.io/ )
         &summary=  
         
         &pics=${imgsrc}
         &site=
         `
      )
      // console.log(33);
    },
    link(){
      let input =document.createElement('input');
      document.body.appendChild(input);
      input.value=location.href;
      input.select();
      document.execCommand('copy');
      input.remove();
      
    },
    qrcode(){
      console.log(55);
      // With promises
      this.erweima('80%','')
    },
    erweima(w,t){
       QRCode.toDataURL(location.href)
        .then(url => {
          let imgage =new Image()
          imgage.src =url;
          this.photoSrc(imgage,w,t)
          console.log(url)
        })
        .catch(err => {
          console.error(err)
        })
    },
    photoSrc(imgage,w,t){
       imgage.style.width=w;
       let div =document.createElement('div');
       let p =document.createElement('p');
       p.innerHTML=t;
       div.className='spareImg'
       document.body.appendChild(div);
       div.addEventListener('click',function(){
        this.remove()
       });
      //  
       div.addEventListener('touchmove',function(){
        this.remove()
       });
      //  
       div.addEventListener('wheel',function(){
        this.remove()
       });
       div.appendChild(imgage);
       div.appendChild(p);
       
    }
   },
  created(){
    // console.log(this.$route.query.id);
    this.pinglun(),
    this.pinglunliebiao()
  }
}
</script>

<style lang="less" scoped>
.xqlist{
  margin-bottom: 52px;
  margin-top: 48px;
}
.msg_heard{
    display: flex;
    height: 45px;
    background-color: rgb(16, 173, 81);
    // line-height: 45px;
    justify-content: space-between;
    align-items: center;
    padding: 0 10px;
    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
    z-index: 999;
    .p{
        color: #fff;
        text-align: center;
    }
}
.content_title{
  margin: 20px 10px;
  h2{width: 360px; word-break: break-all;  //
    }
}
.content_author{
  display: flex;
  justify-content: space-between;
  padding: 10px 10px;
  align-items: center;
  border-bottom: 1px solid rgb(212, 222, 222);
  .content_left{
    display: flex;
    font-size: 8px;
    align-items:stretch;
    .content_time{
      margin-left: 10px;
      p:first-child{
        text-align: left;
        width: 50vw;
        overflow: scroll;
      }
      .time{
      margin-top: 10px;
    }
    }
    
  }
}
.btn_color{
  height: 20px;
  color: #000;
  background-color: #fff;
  padding: 15px;
}
.neirong{
  // width: 360px;
  padding: 10px;
  p{
    width: 360px; 
    // border: 1px solid;
    word-break: break-all;  //
  }

  img{
    margin-top: 10px;
    width: 100%;
    height: 100%;
  }
}
.content_author1{
  display: flex;
  justify-content: space-between;
  padding: 10px 10px;
  align-items: stretch;
  border-bottom: 1px solid rgb(212, 222, 222);
  .content_left{
    display: flex;
    font-size: 8px;
    align-items:stretch;
    .content_time{
      margin-left: 10px;
      h6{
        width: 230px;
        // border: 1px solid;
      }
      .content_btn{
         display: flex;
         align-items: center;
         .huifu{
          margin-left: 10px;
          width: 50px;
          height: 20px;
          font-size: 2px;
          background-color: #fff;
          color: #000;
          border: 1px solid #ccc;
          border-radius: 10px;
         }
      }
      .time{
        margin-top: 5px;
    }
    }
    
  }
}
.content_author2{
  width: 80vw;
  margin: 0 auto;
  display: flex;
  justify-content: space-between;
  padding: 10px 10px;
  align-items: stretch;
  border-bottom: 1px solid rgb(212, 222, 222);
  .content_left{
    display: flex;
    font-size: 8px;
    align-items:stretch;
    // justify-content: space-around;
    .content_time{
      margin-left: 10px;
      h6{
        width: 220px;
        // border: 1px solid;
      }
      .content_btn{
         display: flex;
         align-items: center;
         .huifu{
          margin-left: 10px;
          width: 50px;
          height: 20px;
          font-size: 2px;
          background-color: #fff;
          color: #000;
          border: 1px solid #ccc;
          border-radius: 10px;
         }
      }
      .time{
        margin-top: 5px;
    }
    }
    
  }
}
.xq_footer{
  background: #fff;
  height: 50px;
  border-top: 1px solid rgb(193, 188, 188);
  display: flex;
  justify-content: space-around;
  align-items: center;
  padding-left: 10px;
  position: fixed;
  bottom:0;
  right: 0;
  width: 98%;
  .van-cell{
    padding: 0;
  }
  .btn{
    width: 100%;
    height: 30px;
    background-color: #fff;
    color: #000;
    border: 1px solid ;
  }
  .pinglun{
    height: 30px;
    flex: 3;
    // background-color: #fff;
    // color: #000;
    // border: 1px solid ;
    .text{
      margin-top: 10px;
      padding: 10px;
    }
    .baocun{
      width: 350px;
      margin: 25px auto;
    }
    .van-overlay{
      background-color: rgba(0,0,0,0.1);
    }
  }
  .van-icon{
    margin: 0 10px;
  }
}

</style>